import React from "react";
import styled from "styled-components";

import ROS from "assets/tech/ros.png";
import RASP from "assets/tech/rasp.png";
import ARDU from "assets/tech/ardu.png";
import CPP from "assets/tech/cpp.png";
import MONGO from "assets/tech/mongo.png";
import EXPRESS from "assets/tech/express.png";
import REACT from "assets/tech/react.png";
import NODEJS from "assets/tech/nodejs.png";
import TECHLOGIC from "assets/tech/techLogic.png";

import Navbar from "components/Navbar";
import AboutTitle from "components/AboutTitle";
import Footer from "components/Footer";

const ImgStyled = styled.img`
  width: 200px;
  margin: 0 35px;
`;

const Tech = () => {
  return (
    <>
      <Navbar />
      <AboutTitle
        title="Technology"
        subTitle="We're creating solutions for the future of automated logistics warehouse."
      />
      <div
        style={{
          backgroundColor: "white",
          display: "flex",
          flexDirection: "column",
          justifyContent: "center",
          alignItems: "center",
        }}
      >
        <div
          style={{
            display: "flex",
            flexDirection: "column",
            alignItems: "center",
            margin: "100px 0 0 0",
          }}
        >
          <h1
            style={{
              fontFamily: `"Nunito", sans-serif`,
              marginBottom: "50px",
            }}
          >
            Tech Stack
          </h1>
          <div>
            <ImgStyled src={ROS} alt="ROS" />
            <ImgStyled src={RASP} alt="RASP" />
            <ImgStyled src={ARDU} alt="ARDU" />
            <ImgStyled src={CPP} alt="CPP" />
          </div>
        </div>
        <div>
          <ImgStyled src={MONGO} alt="MONGO" />
          <ImgStyled src={EXPRESS} alt="EXPRESS" />
          <ImgStyled src={REACT} alt="REACT" />
          <ImgStyled src={NODEJS} alt="NODEJS" />
        </div>
      </div>
      <div
        style={{
          display: "flex",
          flexDirection: "column",
          alignItems: "center",
          justifyContent: "center",
        }}
      >
        <h1
          style={{
            marginTop: "100px",
            fontFamily: `"Nunito", sans-serif`,
          }}
        >
          Logic
        </h1>
        <img
          style={{ width: "1000px", marginTop: "50px", marginBottom: "80px" }}
          src={TECHLOGIC}
          alt="TECHLOGIC"
        />
      </div>
      <Footer />
    </>
  );
};

export default Tech;
